<template>
  <div v-if="isVisible" class="NodeToolBox">
    <div class="title">选择节点</div>
    <div class="items iconfont">
      <ul>
        <li
          v-for="(item, i) in NodeList.filter(
            (item) => item.NodeClass == (nodeClass == 1 ? 'App' : 'Agent')
          )"
          :key="i"
          @click="addNode(item.NodeType)"
        >
          <div class="Icon" :class="item.Icon"></div>
          <div class="Name">{{ item.NodeName }}</div>
          <div class="Icon RBt icon-jinrujiantouxiao"></div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script setup></script>

<script>
import Api from "../common/API.js";

import { notify } from "@kyvg/vue3-notification";

export default {
  name: "ToolBox",
  props: {
    node: Object,
    workflowID: String,
    mainID: String,
    isVisible: Boolean,
    nodeClass: Number,
  },
  data() {
    return {
      NodeList: [
        {
          NodeName: "大模型",
          NodeType: "LargeModel",
          Icon: "icon-LargeModel",
          Description: "调用大语言模型，处理变量，配合提示词，生成输出",
          NodeClass: "Agent",
        },
        {
          NodeName: "知识库",
          NodeType: "KnowledgeBase",
          Icon: "icon-KnowledgeBase",
          Description: "根据输入的文本，调用知识库，获取知识库数据",
          NodeClass: "Agent",
        },
        {
          NodeName: "插件",
          NodeType: "Plugins",
          Icon: "icon-Plugins",
          Description: "插件可以提供更多能力，外部操作，获取外部数据等",
          NodeClass: "Agent",
        },
        {
          NodeName: "选择器",
          NodeType: "Selector",
          Icon: "icon-selector1",
          Description: "可以通过选择器的定义，链接多个下游执行节点",
          NodeClass: "Agent",
        },
        {
          NodeName: "Agent",
          NodeType: "Agent",
          Icon: "icon-Agent",
          Description: "调用Agent可以实现更多复杂组合",
          NodeClass: "App",
        },
        {
          NodeName: "时间触发器",
          NodeType: "TimeTrigger",
          Icon: "icon-TimeTrigger",
          Description: "可以定义一个特定时间点来触发下个节点",
          NodeClass: "App",
        },
        /*
        {
          NodeName: "主控AI",
          NodeType: "MainAI",
          Icon: "icon-MainAI",
          Description: "主导应用的核心AI，可以调用多个Agent",
          NodeClass: "App",
        },
        {
          NodeName: "记录员",
          NodeType: "Reporter",
          Icon: "icon-Reporter",
          Description:
            "记录会话内容，并进行必要的摘要与整理，减少回话上下文长度，提高会话质量",
          NodeClass: "App",
        },
*/
      ],
    };
  },
  methods: {
    addNode(NodeType) {
      Api.addNode(NodeType, this.workflowID, this.mainID).then((res) => {
        console.log(res);
        if (res.Status) {
          var Data = res.Data;

          this.$emit("addNode", Data.Config);
        } else {
          notify({ type: "warn", title: "Warn", text: res.ErrorDesc });
        }
      });
    },
  },
};
</script>
