<script setup>
import { Handle, Position } from "@vue-flow/core";

import Tools from "../../common/Tools.js";
</script>
<script>
export default {
  name: "SelectorNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
  },
  created() {},
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      isFold: true,
      isFold_inputs: true,
      isFold_selector: true,
      selectorList: this.data.selector
        ? this.data.selector.map((item, i) => ({
            id: item.id || Tools.guid(),
            inputs: item.inputs || "2",
            comparison: item.comparison || "",
            value: item.value || "",
            varname: item.varname || "",
            top: i * 25 + 240,
            top_str: "top:" + (i * 25 + 240) + "px"
          }))
        : [],
      comparison: [
        { name: "等于", data: "=", text: "等于" },
        { name: "不等于", data: "!=", text: "不等于" },
        { name: "大于", data: ">", text: "大于" },
        { name: "小于", data: "<", text: "小于" },
        { name: "包含", data: "~=", text: "包含" },
        { name: "不包含", data: "!~=", text: "不包含" },
      ],
    };
  },
  methods: {
    resetHandleTop() {
      // 基础高度：标题 + 描述
      var baseHeight = 80;
      
      // 如果主体展开
      if (this.isFold) {
        // 加上输入变量标题
        baseHeight += 40;
        
        // 如果输入变量展开，加上输入变量表格的高度
        if (this.isFold_inputs) {
          // 输入变量表格的基础高度
          baseHeight += 40; // 表头高度
          // 每个输入变量行高48px
          baseHeight += (this.nodeData.inputs?.length || 0) * 48;
        }
        
        // 如果选择器展开
        if (this.isFold_selector) {
          // 加上条件判断标题和表格头部的高度
          baseHeight += 80; // 40(标题) + 40(表头)
          
          // 计算每个选择器的位置
          this.selectorList = this.selectorList.map((item, i) => {
            // 每行高度48px（包含padding和border）
            const top = baseHeight + (i * 48);
            return {
              ...item,
              top: top + 24, // 垂直居中（行高的一半）
              top_str: "top:" + (top + 24) + "px"
            };
          });
        } else {
          // 选择器折叠，只显示标题
          baseHeight += 40;
          // 所有连接点位置重置到标题位置
          this.selectorList = this.selectorList.map(item => ({
            ...item,
            top: baseHeight,
            top_str: "top:" + baseHeight + "px"
          }));
        }
      } else {
        // 整个节点折叠，所有连接点位置重置到标题位置
        baseHeight = 25;
        this.selectorList = this.selectorList.map(item => ({
          ...item,
          top: baseHeight,
          top_str: "top:" + baseHeight + "px"
        }));
      }
    },
    saveNode() {
      // 确保每个选择器都有正确的数据结构
      const cleanSelectorList = this.selectorList.map(item => ({
        id: item.id,
        inputs: item.inputs,
        comparison: item.comparison,
        value: item.value,
        varname: item.varname
      }));

      this.$emit("saveNode", {
        id: this.id,
        data: { 
          ...this.nodeData, 
          label: this.label, 
          selector: cleanSelectorList,
          inputs: this.nodeData.inputs || [] // 确保 inputs 存在
        },
      });

      // 在数据保存后，等待 DOM 更新完成再重新计算位置
      this.$nextTick(() => {
        this.resetHandleTop();
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
      this.resetHandleTop();
    },
    checkFoldInputs() {
      this.isFold_inputs = !this.isFold_inputs;
      this.resetHandleTop();
    },
    checkFoldSelector() {
      this.isFold_selector = !this.isFold_selector;
      this.resetHandleTop();
    },
    onAdd() {
      // 基础高度计算与resetHandleTop保持一致
      let baseHeight = 80; // 标题 + 描述
      
      if (this.isFold) {
        baseHeight += 40; // 输入变量标题
        
        if (this.isFold_inputs) {
          baseHeight += 40; // 输入变量表格标题
          baseHeight += (this.nodeData.inputs?.length || 0) * 48; // 输入变量表格内容
        }
        
        if (this.isFold_selector) {
          baseHeight += 80; // 条件判断标题 + 表头
          // 计算新选择器的位置（每行高度48px）
          const top = baseHeight + (this.selectorList.length * 48);
          
          const newSelector = {
            id: Tools.guid(),
            inputs: "2",
            comparison: "",
            value: "",
            varname: "",
            top: top + 24, // 垂直居中（行高的一半）
            top_str: "top:" + (top + 24) + "px"
          };
          
          this.selectorList.push(newSelector);
          this.saveNode();
        }
      }
    },
    onDel(selector) {
      this.selectorList = this.selectorList.filter((item) => item.id !== selector.id);
      this.saveNode();
    },
    onVarnameSelect(item, selectedVarname) {
      // 直接设置变量名
      item.varname = selectedVarname;
      
      // 查找并复制其他属性
      if (this.data.sourceNodes) {
        for (const sourceNode of Object.values(this.data.sourceNodes)) {
          const outputVar = sourceNode.output.find(out => out.varname === selectedVarname);
          if (outputVar) {
            item.type = outputVar.type || 'string';
            item.txt = outputVar.txt || '';
            break;
          }
        }
      }
      this.saveNode();
    },
    getAvailableVarnames(currentIndex) {
      const availableVarnames = [];
      const usedVarnames = this.nodeData.inputs
        .map((input, index) => (index !== currentIndex ? input.varname : null))
        .filter(Boolean);

      if (this.data.sourceNodes) {
        for (const source of Object.values(this.data.sourceNodes)) {
          if (source.output) {
            source.output.forEach(output => {
              // 如果变量名未被使用
              if (output.varname && (!usedVarnames.includes(output.varname))) {
                availableVarnames.push({
                  varname: output.varname,
                  nodeId: source.nodeId,
                  label: source.label
                });
              }
            });
          }
        }
      }
      return availableVarnames;
    },
    addInput() {
      this.nodeData.inputs.push({
        varname: '',
        type: 'string',
        txt: ''
      });
      this.saveNode();
    },
    removeInput(index) {
      this.nodeData.inputs.splice(index, 1);
      // 确保至少有一个输入
      if (this.nodeData.inputs.length === 0) {
        this.addInput();
      }
      this.saveNode();
    }
  },
  watch: {
    'data.sourceNodes': {
      handler(newSourceNodes, oldSourceNodes) {
        // 当sourceNodes发生变化时（比如删除连线）
        if (oldSourceNodes) {
          const removedNodeIds = Object.keys(oldSourceNodes).filter(id => !newSourceNodes || !newSourceNodes[id]);
          if (removedNodeIds.length > 0) {
            // 找出需要删除的变量
            const inputsToRemove = [];
            this.nodeData.inputs.forEach((input, index) => {
              // 检查每个输入变量是否来自被删除的节点
              for (const nodeId of removedNodeIds) {
                const oldSourceNode = oldSourceNodes[nodeId];
                if (oldSourceNode.output && oldSourceNode.output.some(out => out.varname === input.varname)) {
                  inputsToRemove.push(index);
                  break;
                }
              }
            });
            
            // 从后往前删除变量，避免索引变化的问题
            for (let i = inputsToRemove.length - 1; i >= 0; i--) {
              this.nodeData.inputs.splice(inputsToRemove[i], 1);
            }
            
            // 如果删除后没有输入变量了，添加一个空的
            if (this.nodeData.inputs.length === 0) {
              this.nodeData.inputs.push({
                varname: '',
                type: 'string',
                txt: ''
              });
            }
            
            // 保存更改
            this.saveNode();
          }
        }
      },
      deep: true
    }
  },
};
</script>

<template>
  <Handle
    id="target_1"
    class="Handle"
    type="target"
    :position="Position.Left"
    :style="{}"
  />
  <div v-for="item in selectorList" :key="item.id">
    <Handle
      :id="item.id"
      class="Handle"
      type="source"
      :position="Position.Right"
      :style="item.top_str"
    />
  </div>
  <div class="NodeBox SelectorNode iconfont" :class="[isSelected ? 'Selected' : '', '']">
    <div class="Title">
      <span
        class="Icon fold"
        :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']"
        @click="checkFold"
      ></span>
      <span class="Icon ticon icon-selector1"></span>
      <span>选择器</span>
      <input
        class="label"
        v-model="label"
        value="{{label}}"
        placeholder="备注"
        @blur="saveLabel"
      />
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">可以通过选择器的定义，链接多个下游执行节点</div>
      <div class="Inputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_inputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldInputs"
          ></span>
          <span>输入变量</span>
        </div>
        <div v-if="isFold_inputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>类型</th>
              <th>描述</th>
              <th>操作</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.inputs" :key="i">
                <td>
                  <select 
                    v-model="item.varname" 
                    @change="onVarnameSelect(item, $event.target.value)"
                    class="VarnameSelect"
                  >
                    <option value="" disabled>请选择变量名</option>
                    <option 
                      v-for="varname in getAvailableVarnames(i)" 
                      :key="varname.nodeId + '_' + varname.varname" 
                      :value="varname.varname"
                    >
                      {{ varname.varname }} (来自: {{ varname.label || varname.nodeId }})
                    </option>
                  </select>
                </td>
                <td>
                  <select v-model="item.type" @change="saveNode" class="TypeSelect">
                    <option value="string">字符串</option>
                    <option value="number">数字</option>
                    <option value="boolean">布尔值</option>
                  </select>
                </td>
                <td><input v-model="item.txt" @change="saveNode" class="DescInput" /></td>
                <td>
                  <button 
                    class="DeleteBtn" 
                    @click="removeInput(i)"
                    :disabled="nodeData.inputs.length <= 1"
                    title="至少保留一个输入变量"
                  >
                    删除
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
          <div class="AddBtn" @click="addInput">+</div>
        </div>
      </div>
      <div class="Selector Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_selector ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldSelector"
          ></span>
          <span>条件判断</span>
        </div>
        <div v-if="isFold_selector">
          <table>
            <thead>
              <th>变量名</th>
              <th>判断条件</th>
              <th>比较值</th>
              <th>删除</th>
            </thead>
            <tbody>
              <tr v-for="item in selectorList" :key="item.id">
                <td>
                  <select v-model="item.varname">
                    <option
                      v-for="(item, i) in nodeData.inputs"
                      :key="i"
                      :value="item.varname"
                    >
                      {{ item.varname }}
                    </option>
                  </select>
                </td>
                <td>
                  <select v-model="item.comparison">
                    <option
                      v-for="_item in comparison"
                      :key="_item.name"
                      :value="_item.data"
                    >
                      {{ _item.text }}
                    </option>
                  </select>
                </td>
                <td><input placeholder="留空代表字符串空" v-model="item.value" /></td>
                <td>
                  <span class="Icon ticon icon-del del" @click="onDel(item)"></span>
                </td>
              </tr>
            </tbody>
            <footer>
              <tr>
                <td colspan="4" class="add" @click="onAdd">
                  <span class="Icon ticon icon-add"></span>
                  <span>增加条件</span>
                </td>
              </tr>
            </footer>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

