<script>
import { ref } from "vue";
import { VueFlow, useVueFlow, MarkerType } from "@vue-flow/core";
import { Background } from "@vue-flow/background";
import { ControlButton, Controls } from "@vue-flow/controls";
import { MiniMap } from "@vue-flow/minimap";

import Api from "../common/API.js";
import Tools from "../common/Tools.js";
import ToolIcon from "./ToolIcon.vue";
import ToolBox from "./ToolBox.vue";
import WorkFlowInfo from "./WorkFlowInfo.vue";
import WorkFlowData from "./WorkFlowData.vue";
import LargeModelNode from "./Node/LargeModelNode.vue";
import PluginsNode from "./Node/PluginsNode.vue";
import StartNode from "./Node/StartNode.vue";
import EndNode from "./Node/EndNode.vue";
import SelectorNode from "./Node/SelectorNode.vue";
import KnowledgeBaseNode from "./Node/KnowledgeBaseNode.vue";

import AgentStartNode from "./Node/AgentStartNode.vue";
import AgentEndNode from "./Node/AgentEndNode.vue";

import TimeTrigger from "./Node/TimeTrigger.vue";
import AgentNode from "./Node/AgentNode.vue";
import MainAINode from "./Node/MainAINode.vue";
import ReporterNode from "./Node/ReporterNode.vue";

import CustomEdge from "./Edge/CustomEdge.vue";
import { notify } from "@kyvg/vue3-notification";

var workflow = null;

export default {
  name: "WorkFlow",
  props: {
    baseURL: String,
    workflow_data: {
      title: "智能助理",
      info: null,
      WorkFlowID: "",
      MainID: "",
      MainType: 2,
    },
  },
  components: {
    VueFlow,
    Background,
    MiniMap,
    ToolBox,
    WorkFlowInfo,
    Controls,
    ToolIcon,
    ControlButton,
    WorkFlowData,
    LargeModelNode,
    PluginsNode,
    StartNode,
    EndNode,
    AgentStartNode,
    AgentEndNode,
    SelectorNode,
    TimeTrigger,
    AgentNode,
    MainAINode,
    ReporterNode,
    CustomEdge,
    KnowledgeBaseNode,
  },
  setup() {
    const {
      onInit,
      onNodeDragStop,
      onConnect,
      addEdges,
      setViewport,
      toObject,
      onNodesChange,
      applyNodeChanges,
      onEdgesChange,
      applyEdgeChanges,
    } = useVueFlow();

    onInit((vueFlowInstance) => {
      vueFlowInstance.fitView();
    });
    onNodeDragStop(() => {
      workflow.nodes = toObject().nodes;
    });

    onConnect((connection) => {
      connection.id = Tools.guid();
      connection.markerEnd = MarkerType.ArrowClosed;
      connection.style = "stroke-width: 3;stroke:#3e47de;";
      
      // 获取源节点和目标节点
      const sourceNode = workflow.nodes.find(node => node.id === connection.source);
      const targetNode = workflow.nodes.find(node => node.id === connection.target);
      
      // 如果源节点有output数据，将其传递给目标节点的sourceNodes
      if (sourceNode?.data?.output) {
        if (!targetNode.data.sourceNodes) {
          targetNode.data.sourceNodes = {};
        }
        targetNode.data.sourceNodes[sourceNode.id] = {
          nodeId: sourceNode.id,
          label: sourceNode.data.label,
          output: sourceNode.data.output
        };
      }

      addEdges(connection);
      workflow.edges = toObject().edges;
    });

    onNodesChange((changes) => {
      const nextChanges = [];
      for (const change of changes) {
        if (change.type === "remove") {
          var node = workflow.nodes.filter((item) => item.id === change.id);
          if (node.length > 0) {
            if (node[0].type === "Start" || node[0].type === "AgentStart") {
              notify({ type: "warn", title: "开始节点不能删除" });
              continue;
            } else if (node[0].type === "End" || node[0].type === "AgentEnd") {
              notify({ type: "warn", title: "结束节点不能删除" });
              continue;
            } else if (node[0].type === "MainAI") {
              notify({ type: "warn", title: "应用用主控AI不能删除" });
              continue;
            } else if (node[0].type === "Reporter") {
              notify({ type: "warn", title: "应用记录员不能删除" });
              continue;
            } else {
              nextChanges.push(change);
            }
          }
        } else {
          nextChanges.push(change);
        }
      }
      applyNodeChanges(nextChanges);
    });

    onEdgesChange((changes) => {
      const nextChanges = [];
      for (const change of changes) {
        if (change.type === "remove") {
          if (workflow.selectedNode) {
            continue;
          } else {
            // 在删除边时，找到相关的节点并清理数据
            const edge = workflow.edges.find(e => e.id === change.id);
            if (edge) {
              const targetNode = workflow.nodes.find(node => node.id === edge.target);
              if (targetNode?.data?.sourceNodes) {
                delete targetNode.data.sourceNodes[edge.source];
              }
            }
            nextChanges.push(change);
          }
        } else {
          nextChanges.push(change);
        }
      }
      applyEdgeChanges(nextChanges);
    });

    const _setViewport = ref(setViewport);
    const _toObject = ref(toObject);
    return { _setViewport, _toObject };
  },
  mounted() {
    let _this = this;
    this.init();
    this.startInterval();
    workflow = _this;
  },
  unmount() {
    this.stopInterval();
  },
  data() {
    return {
      ShowNodeBox: false,
      workflow: { title: this.workflow_data.title, info: this.workflow_data.info },
      info: {},
      pluginsList: [], //ref(Plugins),
      largeModelList: [], // ref(LargeModel),
      knowledgeBaseList: [], // ref(KnowledgeBase),
      agentList: [], // ref(Agent),
      nodes: [], // ref(initialNodes),
      edges: [], // ref(initialEdges),
      dark: ref(false),
      selectedNode: null,
      selectEdge: null,
      connectionLineStyle: {
        stroke: "#ddd",
        strokeWidth: 3,
      },

      intervalId: null,
    };
  },
  methods: {
    logToObject() {
      this.save();
    },
    resetTransform() {
      this._setViewport({ x: 0, y: 0, zoom: 1 });
    },
    onShowNodeBox() {
      this.ShowNodeBox = !this.ShowNodeBox;
    },
    toggleDarkMode() {
      this.dark = !this.dark;
    },
    addNode(nodeObj) {
      console.log(nodeObj);
      this.nodes.push(nodeObj);
    },
    saveNode(nodeObj) {
      this.nodes = this.nodes.map((item) => {
        if (item.id == nodeObj.id) {
          item.data = nodeObj.data;
        }
        return item;
      });
    },
    nodeClick(node) {
      this.selectedNode = node;
      this.selectEdge = null;
    },
    edgeClick(edge) {
      this.selectedNode = null;
      this.selectEdge = edge;
    },
    startInterval() {
      this.intervalId = setInterval(this.save, 5000);
    },
    stopInterval() {
      if (this.intervalId) {
        clearInterval(this.intervalId);
      }
    },
    async init() {
      Api.getBaseConfig()
        .then((res) => {
          console.log(res);
          if (res.Status) {
            var Data = res.Data;
            this.largeModelList = ref(Data.largeModelList);
            this.pluginsList = Data.pluginsList;
            this.agentList = Data.agentList;
            this.knowledgeBaseList = Data.knowledgeBaseList;
          }
          this.getWorkFlow();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getWorkFlow() {
      var that = this;
      Api.getWorkFlow(
        that.workflow_data.WorkFlowID,
        that.workflow_data.MainID,
        that.workflow_data.MainType
      )
        .then((res) => {
          if (res.Status) {
            var Data = res.Data;

            this.info = Object.assign({}, this.info, Data.Info);

            Data.Nodes.map((x) => {
              this.nodes.push(x.Config);
            });
            Data.Edges.map((x) => {
              this.edges.push(x.Config);
            });

            this.$forceUpdate();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    save(info = null) {
      var that = this;

      var _WorkFlowID = that.workflow_data.WorkFlowID;
      var _Info = that.info;
      if (info != null) {
        _Info.WorkflowName = info.WorkflowName;
        _Info.Description = info.Description;
      }

      var _Nodes = this._toObject().nodes.map((item) => {
        return {
          NodeID: item.id,
          WorkFlowID: _WorkFlowID,
          NodeType: item.type,
          NodeName: item.data.label,
          Description: item.data.label,
          Config: item,
        };
      });
      var _Edges = this._toObject().edges.map((item) => {
        return {
          EdgeID: item.id,
          WorkFlowID: _WorkFlowID,
          SourceNodeId: item.source,
          TargetNodeId: item.target,
          Condition: "",
          LName: "",
          Config: item,
        };
      });
      var _data = {
        Info: _Info,
        Nodes: _Nodes,
        Edges: _Edges,
        WorkFlowID: _WorkFlowID,
        MainID: that.workflow_data.MainID,
        MainType: that.workflow_data.MainType,
      };

      Api.subWorkFlow(_data)
        .then((res) => {
          console.log(res);
          if (res.Status) {
            that.workflow_data.WorkFlowID = res.Data;
            that.info.LastUpdateTime = new Date().toLocaleString();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<template>
  <VueFlow
    ref="vueFlow"
    :nodes="nodes"
    :edges="edges"
    :class="{ dark }"
    class="basic-flow"
    :default-viewport="{ zoom: 1.5 }"
    :min-zoom="0.2"
    :max-zoom="4"
    :apply-default="false"
    fit-view-on-init
    @nodes-change="onNodesChange"
    @edges-change="onEdgesChange"
    @edgeClick="edgeClick"
    @nodeClick="nodeClick"
    :connection-line-style="connectionLineStyle"
  >
    <Background pattern-color="#f0f0f0" :gap="15" />
    <WorkFlowData class="vue-flow__panel top left"></WorkFlowData>
    <MiniMap position="bottom-left" class="MiniMap"></MiniMap>
    <Controls position="bottom-left">
      <ControlButton title="Reset Transform" @click="resetTransform">
        <ToolIcon name="reset" />
      </ControlButton>

      <ControlButton title="Toggle Dark Mode" @click="toggleDarkMode">
        <ToolIcon v-if="dark" name="sun" />
        <ToolIcon v-else name="moon" />
      </ControlButton>

      <ControlButton title="Log `toObject`" @click="logToObject">
        <ToolIcon name="log" />
      </ControlButton>
    </Controls>
    <Panel class="WorkFlowInfo vue-flow__panel top left vue-flow__controls">
      <WorkFlowInfo v-model:info="info" @save="save"> </WorkFlowInfo>
    </Panel>

    <Panel class="ToolBox vue-flow__panel top left vue-flow__controls">
      <ToolBox
        :is-visible="ShowNodeBox"
        :workflow-i-d="workflow_data.WorkFlowID"
        :main-i-d="workflow_data.MainID"
        :nodeClass="workflow_data.MainType"
        @addNode="addNode"
      ></ToolBox>
      <ControlButton @click="onShowNodeBox">
        <ToolIcon v-if="!ShowNodeBox" name="zoomin" />
        <ToolIcon v-else name="zoomout" />
      </ControlButton>
    </Panel>

    <template #edge-custom="props">
      <CustomEdge
        :id="props.id"
        :source-x="props.sourceX"
        :source-y="props.sourceY"
        :target-x="props.targetX"
        :target-y="props.targetY"
        :source-position="props.sourcePosition"
        :target-position="props.targetPosition"
        :data="props.data"
        :marker-end="props.markerEnd"
        :style="props.style"
      />
    </template>

    <template #node-Start="props">
      <StartNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        @saveNode="saveNode"
      ></StartNode>
    </template>
    <template #node-End="props">
      <EndNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        @saveNode="saveNode"
      ></EndNode>
    </template>

    <template #node-AgentStart="props">
      <AgentStartNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        @saveNode="saveNode"
      ></AgentStartNode>
    </template>
    <template #node-AgentEnd="props">
      <AgentEndNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        @saveNode="saveNode"
      ></AgentEndNode>
    </template>

    <template #node-LargeModel="props">
      <LargeModelNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        :largeModel="largeModelList"
        :plugins="pluginsList"
        @saveNode="saveNode"
      ></LargeModelNode>
    </template>
    <template #node-KnowledgeBase="props">
      <KnowledgeBaseNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        :largeModel="largeModelList"
        :knowledgeBase="knowledgeBaseList"
        @saveNode="saveNode"
      ></KnowledgeBaseNode>
    </template>
    <template #node-Plugins="props">
      <PluginsNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        :plugins="pluginsList"
        @saveNode="saveNode"
      ></PluginsNode>
    </template>
    <template #node-Selector="props">
      <SelectorNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        @saveNode="saveNode"
      ></SelectorNode>
    </template>

    <template #node-TimeTrigger="props">
      <TimeTrigger
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        @saveNode="saveNode"
      ></TimeTrigger>
    </template>

    <template #node-MainAI="props">
      <MainAINode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        :largeModel="largeModelList"
        :plugins="pluginsList"
        @saveNode="saveNode"
      ></MainAINode>
    </template>

    <template #node-Agent="props">
      <AgentNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        :agents="agentList"
        @saveNode="saveNode"
      ></AgentNode>
    </template>

    <template #node-Reporter="props">
      <ReporterNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        :largeModel="largeModelList"
        @saveNode="saveNode"
      ></ReporterNode>
    </template>
  </VueFlow>
</template>
