<script setup>
import SliderInput from "../SliderInput.vue";
import { Check, Close } from "@element-plus/icons-vue";
</script>
<script>
export default {
  name: "ReporterNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
    largeModel: Object,
  },
  components: { SliderInput },
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      largeModelList: this.largeModel,
      isFold: true,
      isFold_inputs: true,
      isFold_outputs: true,
      isFold_prompt: true,
      isFold_largemodel: true,
      isFold_Coefficient: true,
    };
  },
  methods: {
    handleRangeMouseDown() {
      return;
    },
    saveNode() {
      this.$emit("saveNode", {
        id: this.id,
        data: { ...this.nodeData, label: this.label },
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
    },
    checkFoldInputs() {
      this.isFold_inputs = !this.isFold_inputs;
    },
    checkFoldOutputs() {
      this.isFold_outputs = !this.isFold_outputs;
    },
    checkFoldPrompt() {
      this.isFold_prompt = !this.isFold_prompt;
    },
    checkFoldLargeModel() {
      this.isFold_largemodel = !this.isFold_largemodel;
    },
    checkCoefficient() {
      this.isFold_Coefficient = !this.isFold_Coefficient;
    },
  },
};
</script>
<template>
  <div class="NodeBox ReporterNode iconfont" :class="[isSelected ? 'Selected' : '', '']">
    <div class="Title">
      <span
        class="Icon fold"
        :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']"
        @click="checkFold"
      ></span>
      <span class="Icon ticon icon-Reporter"></span>
      <span>记录员</span>
      <input
        class="label"
        v-model="label"
        value="{{label}}"
        placeholder="备注"
        @blur="saveLabel"
      />
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">
        调用大语言模型，记录会话内容，并进行必要的摘要与整理，减少回话上下文长度，提高会话质量
      </div>
      <div class="LargeModel Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_largemodel ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldLargeModel"
          ></span>
          <span>模型</span>
        </div>
        <div v-if="isFold_largemodel">
          <select v-model="nodeData.model.LargeModelID">
            <option
              v-for="(item, i) in largeModelList"
              :key="i"
              :value="item.LargeModelID"
            >
              {{ item.ModelOrganizationName }}({{ item.Name }})
            </option>
          </select>
        </div>
      </div>

      <div class="Prompt Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_prompt ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPrompt"
          ></span>
          <span>提示词（将输入变量与提示词整理，并让AI重新生成返回结果）</span>
        </div>
        <div v-if="isFold_prompt">
          <textarea
            v-model="nodeData.prompt"
            @mousedown.stop="handleRangeMouseDown"
            @wheel.stop="handleRangeMouseDown"
            :placeholder="[
              '请输入提示词：' +
                nodeData.inputs.map((item) => '{{' + item.varname + '}}').join(','),
            ]"
          ></textarea>
        </div>
      </div>
      <div class="Coefficient Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_Coefficient ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkCoefficient"
          ></span>
          <span>系数设置</span>
        </div>
        <div v-if="isFold_Coefficient">
          <el-form label-width="auto" style="max-width: 600px" status-icon>
            <el-form-item label="是否启用">
              <el-switch
                v-model="nodeData.enable"
                class="mt-2"
                style="margin-left: 10px"
                inline-prompt
                :active-icon="Check"
                :inactive-icon="Close"
            /></el-form-item>
            <el-form-item label="随机性(值越大越有创造力)"
              ><SliderInput v-model="nodeData.temperature" min="0" max="100"
            /></el-form-item>
            <el-form-item label="多样性(值越大越有多样性)"
              ><SliderInput v-model="nodeData.topp" min="0" max="100"
            /></el-form-item>
            <el-form-item label="每次总结记录数(会话条数)"
              ><SliderInput v-model="nodeData.recordslength" min="0" max="100"
            /></el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>
