<script setup>
import { Handle, Position } from "@vue-flow/core";
import Multiselect from "vue-multiselect";
import SliderInput from "../SliderInput.vue";
</script>
<script>
export default {
  name: "KnowledgeBaseNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
    knowledgeBase: Object,
    largeModel: Object,
  },
  components: { Multiselect, SliderInput },
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      largeModelList: this.largeModel,
      KnowledgeBaseList: this.knowledgeBase,
      isFold: true,
      isFold_inputs: true,
      isFold_outputs: true,
      isFold_prompt: true,
      isFold_largemodel: true,
      isFold_KnowledgeBase: true,
      isFold_Coefficient: true,
    };
  },
  methods: {
    saveNode() {
      this.$emit("saveNode", {
        id: this.id,
        data: { ...this.nodeData, label: this.label },
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
    },
    checkFoldInputs() {
      this.isFold_inputs = !this.isFold_inputs;
    },
    checkFoldOutputs() {
      this.isFold_outputs = !this.isFold_outputs;
    },
    checkFoldLargeModel() {
      this.isFold_largemodel = !this.isFold_largemodel;
    },
    checkFoldKnowledgeBase() {
      console.log(this.KnowledgeBaseList);
      this.isFold_KnowledgeBase = !this.isFold_KnowledgeBase;
    },
    checkFoldPrompt() {
      this.isFold_prompt = !this.isFold_prompt;
    },
    checkCoefficient() {
      this.isFold_Coefficient = !this.isFold_Coefficient;
    },
    onVarnameSelect(item, selectedVarname) {
      // 直接设置变量名
      item.varname = selectedVarname;
      
      // 查找并复制其他属性
      if (this.data.sourceNodes) {
        for (const sourceNode of Object.values(this.data.sourceNodes)) {
          const outputVar = sourceNode.output.find(out => out.varname === selectedVarname);
          if (outputVar) {
            item.type = outputVar.type || 'string';
            item.txt = outputVar.txt || '';
            break;
          }
        }
      }
      this.saveNode();
    },
    getAvailableVarnames(currentIndex) {
      const availableVarnames = [];
      const usedVarnames = this.nodeData.inputs
        .map((input, index) => (index !== currentIndex ? input.varname : null))
        .filter(Boolean);

      if (this.data.sourceNodes) {
        for (const source of Object.values(this.data.sourceNodes)) {
          if (source.output) {
            source.output.forEach(output => {
              // 如果变量名未被使用
              if (output.varname && (!usedVarnames.includes(output.varname))) {
                availableVarnames.push({
                  varname: output.varname,
                  nodeId: source.nodeId,
                  label: source.label
                });
              }
            });
          }
        }
      }
      return availableVarnames;
    },
    addInput() {
      this.nodeData.inputs.push({
        varname: '',
        type: 'string',
        txt: ''
      });
    },
    removeInput(index) {
      this.nodeData.inputs.splice(index, 1);
      // 确保至少有一个输入
      if (this.nodeData.inputs.length === 0) {
        this.addInput();
      }
      this.saveNode();
    }
  },
  watch: {
    'data.sourceNodes': {
      handler(newSourceNodes, oldSourceNodes) {
        // 当sourceNodes发生变化时（比如删除连线）
        if (oldSourceNodes) {
          const removedNodeIds = Object.keys(oldSourceNodes).filter(id => !newSourceNodes || !newSourceNodes[id]);
          if (removedNodeIds.length > 0) {
            // 找出需要删除的变量
            const inputsToRemove = [];
            this.nodeData.inputs.forEach((input, index) => {
              // 检查每个输入变量是否来自被删除的节点
              for (const nodeId of removedNodeIds) {
                const oldSourceNode = oldSourceNodes[nodeId];
                if (oldSourceNode.output && oldSourceNode.output.some(out => out.varname === input.varname)) {
                  inputsToRemove.push(index);
                  break;
                }
              }
            });
            
            // 从后往前删除变量，避免索引变化的问题
            for (let i = inputsToRemove.length - 1; i >= 0; i--) {
              this.nodeData.inputs.splice(inputsToRemove[i], 1);
            }
            
            // 如果删除后没有输入变量了，添加一个空的
            if (this.nodeData.inputs.length === 0) {
              this.nodeData.inputs.push({
                varname: '',
                type: 'string',
                txt: ''
              });
            }
            
            // 保存更改
            this.saveNode();
          }
        }
      },
      deep: true
    }
  },
};
</script>
<style src="vue-multiselect/dist/vue-multiselect.css"></style>
<template>
  <Handle class="Handle" type="target" :position="Position.Left" :style="{}" />
  <Handle class="Handle" type="source" :position="Position.Right" :style="{}" />
  <div
    class="NodeBox KnowledgeBaseNode iconfont"
    :class="[isSelected ? 'Selected' : '', '']"
  >
    <div class="Title">
      <span
        class="Icon fold"
        :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']"
        @click="checkFold"
      ></span>
      <span class="Icon ticon icon-KnowledgeBase"></span>
      <span>知识库</span>
      <input
        class="label"
        v-model="label"
        value="{{label}}"
        placeholder="备注"
        @blur="saveLabel"
      />
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">根据输入的文本，调用知识库，获取知识库数据</div>

      <div class="Inputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_inputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldInputs"
          ></span>
          <span>输入变量</span>
        </div>
        <div v-if="isFold_inputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>类型</th>
              <th>描述</th>
              <th>操作</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.inputs" :key="i">
                <td>
                  <select 
                    v-model="item.varname" 
                    @change="onVarnameSelect(item, $event.target.value)"
                    class="VarnameSelect"
                  >
                    <option value="" disabled>请选择变量名</option>
                    <option 
                      v-for="varname in getAvailableVarnames(i)" 
                      :key="varname.nodeId + '_' + varname.varname" 
                      :value="varname.varname"
                    >
                      {{ varname.varname }} (来自: {{ varname.label || varname.nodeId }})
                    </option>
                  </select>
                </td>
                <td>
                  <select v-model="item.type" @change="saveNode">
                    <option value="string">字符串</option>
                    <option value="number">数字</option>
                    <option value="boolean">布尔值</option>
                  </select>
                </td>
                <td><input v-model="item.txt" @change="saveNode" /></td>
                <td>
                  <button 
                    class="DeleteBtn" 
                    @click="removeInput(i)"
                    :disabled="nodeData.inputs.length <= 1"
                    title="至少保留一个输入变量"
                  >
                    删除
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
          <div class="AddBtn" @click="addInput">+</div>
        </div>
      </div>

      <div class="KnowledgeBase Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_KnowledgeBase ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldKnowledgeBase"
          ></span>
          <span>知识库</span>
        </div>
        <div v-if="isFold_KnowledgeBase">
          <multiselect
            v-model="nodeData.knowledgeBase"
            placeholder="请选择知识库"
            :multiple="true"
            :close-on-select="false"
            :clear-on-select="false"
            :preserve-search="true"
            label="Name"
            track-by="Name"
            taggable="true"
            :options="KnowledgeBaseList"
          ></multiselect>
        </div>
      </div>

      <div class="LargeModel Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_largemodel ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldLargeModel"
          ></span>
          <span>模型</span>
        </div>
        <div v-if="isFold_largemodel">
          <select v-model="nodeData.model.LargeModelID">
            <option
              v-for="(item, i) in largeModelList"
              :key="i"
              :value="item.LargeModelID"
            >
              {{ item.ModelOrganizationName }}({{ item.Name }})
            </option>
          </select>
        </div>
      </div>
      <div class="Prompt Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_prompt ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPrompt"
          ></span>
          <span>提示词（将输入变量与提示词整理，并让AI重新生成返回结果）</span>
        </div>
        <div v-if="isFold_prompt">
          <textarea
            v-model="nodeData.prompt"
            @mousedown.stop="handleRangeMouseDown"
            @wheel.stop="handleRangeMouseDown"
            :placeholder="[
              '请输入提示词：' +
                nodeData.inputs.map((item) => '{{' + item.varname + '}}').join(','),
            ]"
          ></textarea>
        </div>
      </div>
      <div class="Coefficient Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_Coefficient ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkCoefficient"
          ></span>
          <span>系数设置</span>
        </div>
        <div v-if="isFold_Coefficient">
          <el-form label-width="auto" style="max-width: 600px" status-icon>
            <el-form-item label="随机性(值越大越有创造力)"
              ><SliderInput v-model="nodeData.temperature" min="0" max="100"
            /></el-form-item>
            <el-form-item label="多样性(值越大越有多样性)"
              ><SliderInput v-model="nodeData.topp" min="0" max="100"
            /></el-form-item>
          </el-form>
        </div>
      </div>
      <div class="Outputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_outputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldOutputs"
          ></span>
          <span>输出</span>
        </div>
        <div v-if="isFold_outputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.output" :key="i">
                <td><input v-model="item.varname" /></td>
                <td><input v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

