<script setup>
import { Handle, Position } from "@vue-flow/core";
</script>
<script>
export default {
  name: "TimeTriggerNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
  },
  created() {},
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      isFold: true,
      isFold_outputs: true,
      isFold_TimeTrigger: true,
      isFold_prompt: true,

      loopType: [
        { name: "每隔n秒", data: "s", text: "每n秒" },
        { name: "每隔n天", data: "d", text: "每n天" },
        { name: "每周星期几", data: "w", text: "每周星期几" },
        { name: "每月第几日", data: "m", text: "每月第几日" },
      ],
    };
  },
  methods: {
    handleRangeMouseDown() {
      return;
    },
    saveNode() {
      this.$emit("saveNode", {
        id: this.id,
        data: { ...this.nodeData, label: this.label },
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
      this.resetHandleTop();
    },
    checkFoldOutputs() {
      this.isFold_outputs = !this.isFold_outputs;
    },
    checkFoldTimeTrigger() {
      this.isFold_TimeTrigger = !this.isFold_TimeTrigger;
      this.resetHandleTop();
    },
    checkFoldPrompt() {
      this.isFold_prompt = !this.isFold_prompt;
    },
  },
};
</script>

<template>
  <Handle class="Handle" type="source" :position="Position.Right" :style="{}" />
  <div
    class="NodeBox TimeTriggerNode iconfont"
    :class="[isSelected ? 'Selected' : '', '']"
  >
    <div class="Title">
      <span
        class="Icon fold"
        :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']"
        @click="checkFold"
      ></span>
      <span class="Icon ticon icon-TimeTrigger"></span>
      <span>时间触发器</span>
      <input
        class="label"
        v-model="label"
        value="{{label}}"
        placeholder="备注"
        @blur="saveLabel"
      />
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">可以定义一个特定时间点来触发下个节点</div>

      <div class="TimeTrigger Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_TimeTrigger ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldTimeTrigger"
          ></span>
          <span>触发周期</span>
        </div>
        <div v-if="isFold_TimeTrigger">
          <el-form label-width="auto" style="max-width: 600px" status-icon>
            <el-form-item label="循环类型">
              <el-select v-model="nodeData.timeTrigger.loopType" placeholder="循环类型">
                <el-option
                  v-for="_item in loopType"
                  :key="_item.name"
                  :value="_item.data"
                  :label="_item.text"
                />
              </el-select>
            </el-form-item>
            <el-form-item label="周期">
              <el-input
                v-model="nodeData.timeTrigger.IntervalValue"
                type="number"
                placeholder="循环类型N的值"
              />
            </el-form-item>
            <el-form-item
              label="第一次开始时间"
              title="循环类型为日、周、月时该值的时间'hh:mm'为每次循环的开始时间"
            >
              <el-date-picker
                v-model="nodeData.timeTrigger.StartTime"
                type="datetime"
                placeholder="选择"
                :default-time="defaultTime"
              />
            </el-form-item>
            <el-form-item label="执行次数">
              <el-input
                v-model="nodeData.timeTrigger.RepeatValue"
                type="number"
                placeholder="0等于无限次"
              />
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="Prompt Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_prompt ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPrompt"
          ></span>
          <span>提示词（将输入变量与提示词整理，作为下个节点的输入参数）</span>
        </div>
        <div v-if="isFold_prompt">
          <textarea
            v-model="nodeData.prompt"
            @mousedown.stop="handleRangeMouseDown"
            @wheel.stop="handleRangeMouseDown"
            :placeholder="['请输入提示词：{{currentTime}}']"
          ></textarea>
        </div>
      </div>
      <div class="Outputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_outputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldOutputs"
          ></span>
          <span>输出</span>
        </div>
        <div v-if="isFold_outputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.output" :key="i">
                <td><input v-model="item.varname" /></td>
                <td><input v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
