<template>
  <div class="slider-input">
    <input
      type="range"
      :min="min"
      :max="max"
      v-model="inputValue"
      @input="updateValue($event.target.value)"
      @mousedown.stop="handleRangeMouseDown"
    />
    <span>{{ inputValue }}</span>
  </div>
</template>

<script>
export default {
  props: {
    modelValue: {
      type: Number,
      default: 0,
    },
    min: {
      type: Number,
      default: 0,
    },
    max: {
      type: Number,
      default: 100,
    },
  },
  setup() {
    const handleRangeMouseDown = () => {
      return;
    };

    return { handleRangeMouseDown };
  },
  data() {
    return {
      inputValue: this.modelValue,
    };
  },
  watch: {
    modelValue(newValue) {
      this.inputValue = newValue;
    },
  },
  methods: {
    updateValue(value) {
      this.inputValue = value;
      this.$emit("update:modelValue", value);
    },
  },
};
</script>

<style>
.slider-input{
width: 100%;}
.slider-input input {
  width: 100%;
}
.slider-input span {
  margin-left: 10px;
}
</style>
