import axios from "axios";

const apiClient = axios.create({
  baseURL: "/", //
  headers: {
    "Content-Type": "application/json",
  },
});

class ApiService {
  static get(url) {
    return apiClient.get(url).then((response) => response.data);
  }

  static post(url, data, config) {
    return apiClient.post(url, data, config).then((response) => response.data);
  }

  static put(url, data) {
    return apiClient.put(url, data).then((response) => response.data);
  }

  static delete(url) {
    return apiClient.delete(url).then(() => true);
  }

  static getBaseConfig() {
    return this.get("/Manage/Workflow/getBaseConfig");
  }

  static getWorkFlow(WorkflowID, MainID, MainType) {
    return this.post(
      "/Manage/Workflow/getWorkFlow",
      {
        WorkflowID,
        MainID,
        MainType,
      },
      {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8", // 设置请求头，确保服务器正确解析 FormData
        },
      }
    );
  }

  static addNode(NodeType, WorkflowID, MainID) {
    return this.post(
      "/Manage/Workflow/addNode",
      {
        WorkflowID,
        NodeType,
        MainID,
      },
      {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8", // 设置请求头，确保服务器正确解析 FormData
        },
      }
    );
  }

  static subWorkFlow(Data) {
    return this.post("/Manage/Workflow/Save", Data, {
      headers: {
        "Content-Type": "application/json",
      },
    });
  }

  static getNodeExcutionRecord(workflowID, nodeID) {
    return this.post("getNodeExcutionRecord", { workflowID, nodeID });
  }

  static getChatLog(ChatSessionID) {
    return this.post("getChatLog", { ChatSessionID });
  }
}

export default ApiService;
