<script setup>
import { Handle, Position } from "@vue-flow/core";
</script>
<script>
export default {
  name: "StartNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
  },
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      isFold: true,
      isFold_inputs: true,
      isFold_outputs: true,
      isFold_prompt: true,
    };
  },
  methods: {
    handleRangeMouseDown() {
      return;
    },
    saveNode() {
      this.$emit("saveNode", {
        id: this.id,
        data: { ...this.nodeData, label: this.label },
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
    },
    checkFoldInputs() {
      this.isFold_inputs = !this.isFold_inputs;
    },
    checkFoldOutputs() {
      this.isFold_outputs = !this.isFold_outputs;
    },
    checkFoldPrompt() {
      this.isFold_prompt = !this.isFold_prompt;
    },
  },
};
</script>
<template>
  <Handle class="Handle" type="source" :position="Position.Right" :style="{}" />
  <div class="NodeBox StartNode iconfont" :class="[isSelected ? 'Selected' : '', '']">
    <div class="Title">
      <span
        class="Icon fold"
        :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']"
        @click="checkFold"
      ></span>
      <span class="Icon ticon icon-start"></span>
      <span>开始</span>
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">工作流的起始节点，用于设定启动工作流需要的信息</div>
      <div class="Inputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_inputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldInputs"
          ></span>
          <span>输入</span>
        </div>
        <div v-if="isFold_inputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.inputs" :key="i">
                <td><input v-model="item.varname" /></td>
                <td><input v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="Prompt Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_prompt ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPrompt"
          ></span>
          <span>提示词</span>
        </div>
        <div v-if="isFold_prompt">
          <textarea
            v-model="nodeData.prompt"
            @mousedown.stop="handleRangeMouseDown"
            @wheel.stop="handleRangeMouseDown"
            :placeholder="[
              '请输入提示词：' +
                nodeData.inputs.map((item) => '{{' + item.varname + '}}').join(','),
            ]"
          ></textarea>
        </div>
      </div>
      <div class="Outputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_outputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldOutputs"
          ></span>
          <span>输出</span>
        </div>
        <div v-if="isFold_outputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.output" :key="i">
                <td><input v-model="item.varname" /></td>
                <td><input v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
