<template>
  <div class="WorkFlowInfo" @click="toggleDialog">
    <div class="Title">{{ info.WorkflowName }}</div>
    <div class="LastUpdateTime">{{ new Date(info.LastUpdateTime).toLocaleString() }}</div>
  </div>

  <el-dialog v-model="dialogFormVisible" title="工作流信息" width="500">
    <el-form :model="form" label-width="auto">
      <el-form-item label="工作流名称" label-position="right">
        <el-input v-model="Info.WorkflowName" autocomplete="off" />
      </el-form-item>
      <el-form-item label="说明" label-position="right">
        <el-input
          v-model="Info.Description"
          show-word-limit
          maxlength="128"
          type="textarea"
        ></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="closeDialog">取消</el-button>
        <el-button type="primary" @click="save">保存</el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import { ref, onMounted, watch } from "vue";

export default {
  name: "WorkFlowInfo",
  props: {
    node: Object,
    workflowID: String,
    isVisible: Boolean,
    info: Object,
  },
  setup(props) {
    const dialogFormVisible = ref(false);
    const Info = ref({});

    onMounted(() => {
      Info.value = { ...props.info };
    });
    watch(
      () => props.info,
      (newInfo) => {
        Info.value = { ...newInfo };
      },
      { immediate: true }
    );
    const toggleDialog = () => {
      dialogFormVisible.value = !dialogFormVisible.value;
    };

    const closeDialog = () => {
      dialogFormVisible.value = false;
    };

    return { dialogFormVisible, Info, toggleDialog, closeDialog };
  },
  methods: {
    show() {
      this.dialogFormVisible = !this.dialogFormVisible;
    },
    save() {
      this.$emit("save", this.Info);
      this.dialogFormVisible = false;
    },
  },
};
</script>
